@import "../../css/constants";

.detailContainer {
  position: fixed;
  top: $nav-xsmall-height;
  background: white;
  width: 100%;
  height: 100%;
  overflow-y: scroll;

  .backButtonContainer {
    display: none;
  }

  .image {
    position: relative;
    width: 100%;
  }

  .artDetail {
    padding: 0 $category-card-small-margin;
  }

  .comments {
    padding: $category-card-small-margin;
    padding-bottom: 2 * $nav-xsmall-height;
    .title {
      font-weight: bold;
    }

  }

  .commentBox {
    position: fixed;
    background: white;
    left: 0;
    bottom: 0;
    height: calc(#{$nav-xsmall-height});
    border-top: 1px solid rgba(100, 100, 100, 0.1);
    width: 100%;
    display: flex;
    align-items: center;
    padding: $category-card-small-margin;

    input {
      flex-grow: 8;
      height: 100%;
      border: 1px solid rgba(100, 100, 100, 0.2);
      border-radius: $nav-xsmall-height;
      outline: none;
      padding: $category-card-small-margin *2;

      &:focus {
        border-color: #2196f3ff;
      }
    }

    span {
      flex-grow: 2;
      text-align: center;
    }
  }

  @media screen and (min-width: $sm){
    padding: 0 4*$category-card-small-margin;
    .commentBox {
      left: calc(#{$nav-small-width} + #{$category-card-small-margin} * 4);
      width: calc(100% - #{$nav-small-width} - 8*#{$category-card-small-margin});
    }
  }

  @media screen and (min-width: $md){
    position: fixed;
    left: 0;
    top: 0;
    z-index: 3;
    padding: 0;
    display: flex;

    & > .image {
      width: 50%;
      margin: 0;
      background: rgba(0, 0, 0, 0.8);
      display: flex;
      align-items: center;
      .card {
        width: calc(100% - #{2*$category-card-small-margin});
        .image {

        }
      }
    }

    .detail {
      width: 50%;
      margin: 2*$category-card-small-margin;
    }

    .commentBox {
      left: 50%;
      width: 50%;
    }

    .backButtonContainer {
      display: block;
      padding-bottom: $category-card-small-margin;
    }
  }
}
