@import "../../css/constants";

.comment {
  margin: $category-card-small-margin 0;
  border: 1px solid rgba(100, 100, 100, 0.1);
  padding: calc(#{ $category-card-small-margin / 2}) $category-card-small-margin;
  border-radius: $category-card-small-margin;
  width: calc(100%);
  .author {
    font-weight: bold;
    .time {
      font-weight: normal;
      float: right;
      color: grey;
    }
  }
}
