@import "../../css/constants";
.card {
  width: calc(100% - #{2*$category-card-small-margin});
  margin: $category-card-small-margin;
  margin-bottom: calc(#{$category-card-small-margin} + 0.5rem);
  .image {
    img {
      max-width: 100%;
    }
  }

  .actions {
    background: white;
    padding: $category-card-small-margin;
    display: flex;
    align-items: center;
    .action {
      padding: 0.25rem 1rem;
      &:nth-child(1) {
        flex-grow: 1;
      }
    }
  }

  @media screen and (min-width: $sm){
    display: inline-block;
    width: calc(50% - #{2*$category-card-small-margin});
  }

  @media screen and (min-width: $md){
    width: calc(33% - #{3*$category-card-small-margin});
  }

  @media screen and (min-width: $lg){

  }
}
